import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom/client'
import styled from 'styled-components'
import html2canvas from 'html2canvas'
import './index.css'
import App from './App'

const root = ReactDOM.createRoot(document.getElementById('root'))

const Main = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  
  @media (max-width: 800px) {
    flex-direction: column;
  }
`

const Overflow = styled.div`
  overflow: hidden;
  position: relative;
  border-radius: 10px;
  background-color: #f7f7f7;
  justify-content: center;
  align-items: center;
  display: flex;
  width: 280px;
  height: 598px;
  box-shadow: 0px 3px 41px 15px #e0e0e0;

  @media (max-width: 800px) {
    margin-top: 20px;
  }
`

const Iframe = styled.div`
  transform: scale(.7);
  min-width: 400px;
  width: 400px;
  min-height: 854px;
  height: 854px;
`

const NavigationContainer = styled.div`
  margin-left: 60px;
  margin-top: 0px;
  margin-bottom: 0px;
  width: 100%;
  max-width: 400px;
  box-shadow: 0px 3px 41px 15px #e0e0e0;
  border-radius: 20px;
  padding: 20px;
  box-sizing: border-box;
  background: #fff;

  @media (max-width: 800px) {
    margin-left: 0px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
`

const NavigationLine = styled.div`
  width: 100%;
  height: 1px;
  background: #e0e0e0aa;
  margin: 8px 0px 20px 0px;
`

const PhoneContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`

const ScreenShotButton = styled.div`
  margin-top: 20px;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0px 3px 41px 15px #e0e0e0;
  background: #fff;
  cursor: pointer;
  transition: 0.2s;
  user-select: none;
  position: relative;
  z-index: 99999999;

  &:active {
    box-shadow: 0px 0px 0px 0px #e0e0e0;
  }
`

const ScreenShot = styled.div`
  min-width: 400px;
  width: 400px;
  min-height: 854px;
  height: 854px;
  background: #fff;
  position: absolute;
  font-size: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
`

const Button = styled.div`
  display: inline-flex;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0px 2px 9px 0px #e0e0e0;
  background: #fff;
  cursor: pointer;
  transition: 0.2s;
  user-select: none;
  margin-right: 12px; 
  margin-bottom: 12px; 

  &:active {
    box-shadow: 0px 0px 0px 0px #e0e0e0;
  }
`

const Input = styled.input`
  border: none;
  outline: none;
  display: inline-flex;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0px 2px 9px 0px #e0e0e0;
  background: #fff;
  cursor: pointer;
  transition: 0.2s;
  user-select: none;
  margin-right: 12px;
  margin-bottom: 12px; 

  &:active {
    box-shadow: 0px 0px 0px 0px #e0e0e0;
  }
`

const Type = styled.div`
  display: inline-flex;
  border-radius: 10px;
  padding: 10px 0px;
  user-select: none;
  margin-right: 12px; 
  margin-bottom: 12px; 
`

const MainPage = () => {
  const [isScreenShot, setScreenShot] = useState(false)
      , [message, setMessage] = useState({
          type: 0,
          fromMe: true,
          time: "",
          date: "",
          text: "",
          file: {
            name: "",
            size: "",
            src: ""
          },
          reply: {
            username: "",
            text: ""
          }
        })
      , [config, setConfig] = useState(null)

  useEffect(() => {
    fetch('config.json')
      .then(d => d.json())
      .then(j => setConfig(j))
  }, [])

  window.getConfig = () => config

  return config ? (
    <Main>
      <PhoneContainer>
        <Overflow>
          <Iframe style={{ transform: isScreenShot ? 'scale(1)' : 'scale(.7)' }}>
            <App 
              config={config} 
              onRemoveMessage={
                _id => {
                  if (window.confirm('Удалить сообщение ?')) {
                    setConfig(
                      config => ({ 
                        ...config, 
                        messages: config.messages.filter(({ id }) => id !== _id) 
                      })
                    )
                  }
                }
              }   
            />
          </Iframe>
          {
            isScreenShot 
              ? (
                <ScreenShot>📸</ScreenShot>
              )
              : (
                null
              )
          }
        </Overflow>
        <ScreenShotButton 
          onClick={
            () => {
              setScreenShot(true)
              
              const timeId = setInterval(() => {
                setScreenShot(isScreenShot => {
                  if (isScreenShot) {
                    clearInterval(timeId)
                    
                    html2canvas(document.querySelector('#iphone')).then(canvas => {
                      let canvasUrl = canvas.toDataURL('image/jpeg', 0.5)
                      const createEl = document.createElement('a')
                      createEl.href = canvasUrl
                      createEl.download = 'screenshot.jpeg'
                      createEl.click()
                      createEl.remove()
                      setScreenShot(false)
                    })
                  }

                  return isScreenShot
                })
                
              }, 100)
            }
          }
        >
          Screenshot 📱
        </ScreenShotButton>
      </PhoneContainer>
      <NavigationContainer>
        <Input 
          style={{ width: '40px' }}
          value={config.time} 
          onChange={({ target: { value } }) => setConfig(config => ({ ...config, time: value }))} 
          placeholder='Время'
        />
        <Input 
          value={config.username} 
          onChange={({ target: { value } }) => setConfig(config => ({ ...config, username: value }))} 
          placeholder='Имя'
        />
        <label htmlFor="photo">
          <input 
            hidden={true}
            id="photo"
            type='file' 
            onChange={event => {
              const file = new FileReader()
              
              file.onload = () => {
                setConfig(
                  config => ({
                    ...config,
                    image: file.result
                  })
                )
              }

              file.readAsDataURL(event.target.files[0])
            }}
          />
          <Button>📷 Аватарка</Button>
        </label>
        <NavigationLine />
          <Type>Тип сообщения: </Type>
          <Button onClick={() => setMessage(m => ({ ...m, type: 0 }))}>Текст {message.type === 0 ? '✅' : ''}</Button>
          <Button onClick={() => setMessage(m => ({ ...m, type: 1 }))}>Ответ {message.type === 1 ? '✅' : ''}</Button>
          <Button onClick={() => setMessage(m => ({ ...m, type: 2 }))}>Файл {message.type === 2 ? '✅' : ''}</Button>
        {
          message.type !== 3 
            ? (
              <>
                <NavigationLine />
                <Type>От: </Type>
                <Button onClick={() => setMessage(m => ({ ...m, fromMe: false }))}>Меня {!message.fromMe ? '✅' : ''}</Button>
                <Button onClick={() => setMessage(m => ({ ...m, fromMe: true }))}>Собеседника {message.fromMe ? '✅' : ''}</Button>
              </>
            )
            : (
              null
            )
        }
        <NavigationLine />
        {
          message.type === 0
            ? (
              <>
                <Input 
                  value={message.text} 
                  onChange={({ target: { value } }) => setMessage(message => ({ ...message, text: value }))} 
                  placeholder='Сообщение'
                />
                <Input 
                  style={{ width: '40px' }}
                  value={message.time} 
                  onChange={({ target: { value } }) => setMessage(message => ({ ...message, time: value }))} 
                  placeholder='Время'
                />
              </>
            )
            : message.type === 1 
                ? (
                  <>
                    <Input 
                      value={message.reply.text} 
                      onChange={
                        ({ target: { value } }) => 
                          setMessage(
                            message => ({ 
                              ...message, 
                              reply: { 
                                ...message.reply, 
                                text: value 
                              } 
                            })
                          )
                      } 
                      placeholder='Сообщение'
                    />
                    <Input 
                      value={message.reply.username} 
                      onChange={
                        ({ target: { value } }) => 
                          setMessage(
                            message => ({ 
                              ...message, 
                              reply: { 
                                ...message.reply, 
                                username: value 
                              } 
                            })
                          )
                      } 
                      placeholder='Имя'
                    />
                    <Input 
                      style={{ width: '40px' }}
                      value={message.time} 
                      onChange={({ target: { value } }) => setMessage(message => ({ ...message, time: value }))} 
                      placeholder='Время'
                    />
                    <Input 
                      value={message.text} 
                      onChange={({ target: { value } }) => setMessage(message => ({ ...message, text: value }))} 
                      placeholder='Ответ'
                    />
                  </> 
                )
                : message.type === 2
                    ? (
                      <>
                        <Input
                          value={message.file.name} 
                          onChange={({ target: { value } }) => setMessage(message => ({ ...message, file: { ...message.file, name: value } }))} 
                          placeholder='Название'
                        />
                        <Input
                          value={message.file.size} 
                          onChange={({ target: { value } }) => setMessage(message => ({ ...message, file: { ...message.file, size: value } }))} 
                          placeholder='Размер'
                        />
                        <label htmlFor="preview">
                          <input 
                            hidden={true}
                            id="preview"
                            type='file' 
                            onChange={event => {
                              const file = new FileReader()
                              
                              file.onload = () => {
                                setMessage(
                                  message => ({
                                    ...message,
                                    file: {
                                      ...message.file,
                                      src: file.result
                                    }
                                  })
                                )
                              }

                              file.readAsDataURL(event.target.files[0])
                            }}
                          />
                          <Button>Превью</Button>
                        </label>
                        <Input 
                          style={{ width: '40px' }}
                          value={message.time} 
                          onChange={({ target: { value } }) => setMessage(message => ({ ...message, time: value }))} 
                          placeholder='Время'
                        />
                      </>
                    )
                    : (
                      null
                    )
        }
        <NavigationLine />
        <Button 
          onClick={
            () => 
              setConfig(
                config => ({
                  ...config,
                  messages: [
                    ...config.messages,
                    (type => {
                      if (type === 0) {
                        return {
                          fromMe: message.fromMe,
                          text: message.text,
                          time: message.time,
                          id: parseInt(Math.random() * 100000)
                        }
                      }

                      if (type === 1) {
                        return {
                          fromMe: message.fromMe,
                          text: message.text,
                          time: message.time,
                          reply: message.reply,
                          id: parseInt(Math.random() * 100000)
                        }
                      }

                      if (type === 2) {
                        return {
                          fromMe: message.fromMe,
                          time: message.time,
                          file: message.file,
                          id: parseInt(Math.random() * 100000)
                        }
                      }
                    })(message.type)
                  ]
                })
              )
          }
        >
          Отправить
        </Button>
        <Button 
          onClick={
            () => 
              setConfig(
                config => ({
                  ...config,
                  messages: []
                })
              )
          }
        >
          Удалить историю
        </Button>

      </NavigationContainer>
    </Main>
  ) : null
}

root.render(
  <MainPage />
)
