const FirstMessage = ({ text, file, reply, time, isOpponent, onClick }) => (
  <OpponentMessage isOpponent={isOpponent} onClick={onClick}>
    <MessageHiddeWrapper>
      <MessageWhiteWrapper
        style={{
          borderRadius: isOpponent ? '14px 14px 9px 14px' : '14px 14px 14px 9px',
          padding: file ? '6.5px 10px 6.5px 7.5px' : '6.5px 10px 6.5px 12px',
          marginLeft: isOpponent ? '0px' : '6px',
          marginRight: isOpponent ? '6px' : '0px',
          background: isOpponent ? '#E1FEC6' : '#FFFFFF'
        }}
      >
        {
          file
            ? (
              <FileWrapper>
                <FileIcon src={file.src} />
                <FileMetaContainer>
                  <FileTitle style={{ color: isOpponent ? '#2DA430' : '#007AFF' }}>{file.name}</FileTitle>
                  <FileSize style={{ color: isOpponent ? '#2DA430' : '#007AFF' }}>{file.size}</FileSize>
                </FileMetaContainer>
              </FileWrapper>
            )
            : (
              null
            )
        }
        {
          reply
            ? (
              <ReplyWrapper>
                <ReplyMessage>
                  <ReplyLine style={{ background: isOpponent ? '#2DA430' : '#007AFF' }} />
                  <ReplyMessageContainer>
                    <ReplyUsername style={{ color: isOpponent ? '#2DA430' : null }}>{reply.username}</ReplyUsername>
                    <ReplyTextMessage>{(t => t.length > 29 ? t.slice(0, 26)+'...' : t)(reply.text)}</ReplyTextMessage>
                  </ReplyMessageContainer>
                </ReplyMessage>
                <TextMessage>{text}</TextMessage>
              </ReplyWrapper>
            )
            : (
              <TextMessage>{text}</TextMessage>
            )
          }
        <MessageTime style={{ color: isOpponent ? '#2DA430' : '#8E8E93' }}>{time}</MessageTime>
        {
          isOpponent
            ? <DoneMessage src={messageDoneIcon} />
            : null
        }
      </MessageWhiteWrapper>
    </MessageHiddeWrapper>
  </OpponentMessage>
)

