const App = ({ config, onRemoveMessage }) => {
  const ref = useRef()
  const refMessager = useRef()

  const [scroll, setScroll] = useState(true)

  useEffect(() => {
    const node = ref.current

    if (node) {
      window.TWallpaper.init(node)
      window.TWallpaper.animate(true)
      const timeid = setInterval(() => {
        window.TWallpaper.update()
      }, 5000)

      return () => clearInterval(timeid)
    }
  }, [ref, config])

  useEffect(() => {
    const node = refMessager.current

    if (node) {
      const handlerScroll = () => {
        setScroll(node.scrollTop < node.scrollHeight - node.clientHeight - 70)
      }

      node.addEventListener('scroll', handlerScroll)
      return () => node.removeEventListener('scroll', handlerScroll)
    }
  }, [refMessager, config])

  return config ? (
    <Body id='iphone'>
      <FrontWrapper>
        <BackgroundColor>
          <StatusBar>
            <Time>{config.time}</Time>
            <Status src={statusIcons} />
          </StatusBar>
          <NavBar>
            <Back src={backIcon} />
            <Wrapper>
              <TitleName>
                {config.username}
              </TitleName>
              <OnlineStatus>
                last seen just now
              </OnlineStatus>
            </Wrapper>
            <Avatar src={config.image} />
          </NavBar>
        </BackgroundColor>
        <Messager ref={refMessager}>
          {
            config.messages.map((message, index) => {
              if (!config.messages[index-1]) {
                return (
                  <FirstMessage onClick={() => onRemoveMessage(message.id)} key={index+JSON.stringify(message)} {...message} text={message.text} time={message.time} isOpponent={!message.fromMe} />
                )
              }

              if (
                (
                  config.messages[index+1] && config.messages[index+1].fromMe !== message.fromMe
                ) || !config.messages[index+1]
              ) {
                return (
                  <LastMessage onClick={() => onRemoveMessage(message.id)} key={index+JSON.stringify(message)} {...message} text={message.text} time={message.time} isOpponent={!message.fromMe} style={{ marginBottom: '4px' }} />
                )
              }

              if (config.messages[index-1] && config.messages[index-1].fromMe === message.fromMe) {
                return (
                  <MiddleMessage onClick={() => onRemoveMessage(message.id)} key={index+JSON.stringify(message)} {...message} text={message.text} time={message.time} isOpponent={!message.fromMe} />
                )
              } else {
                return (
                  <FirstMessage onClick={() => onRemoveMessage(message.id)} key={index+JSON.stringify(message)} {...message} text={message.text} time={message.time} isOpponent={!message.fromMe} />
                )
              }
            })
          }
        </Messager>
        <ScrolledButton animate={{ bottom: scroll ? '89px' : '0px' }}>
          <ScrolledButtonIcon src={arrowIcon} />
        </ScrolledButton>
        <BackgroundColor>
          <MessageBar>
            <AttachButton src={attachIcon} />
            <MessageInput src={messageInputIcon} />
            <MicButton src={micIcon} />
          </MessageBar>
          <HomeButtonBar>
            <HomeLine />
          </HomeButtonBar>
        </BackgroundColor>
      </FrontWrapper>
      <BackgroundImage></BackgroundImage>
    </Body>
  ) : (
    null
  )
}

export default App